import styles from "../css/result-table.module.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTimes, faCheck } from "@fortawesome/free-solid-svg-icons";

function EditRow({
  retailerId,
  retailerName,
  retailerPhoneNo,
  retailerEmail,
  retailerDetail,
  editFormData,
  handleEditFormChange,
  handleCancelClick,
}) {
  return (
    <tr key={retailerId} className={styles.screenPage__searchResultTable_items}>
      <td key={"retailerName_edit"}>
        <input
          type="text"
          required="required"
          placeholder={retailerName}
          name="retailerName"
          value={editFormData.retailerName}
          onChange={handleEditFormChange}
        ></input>
      </td>
      <td key={"retailerPhone_edit"}>
        <input
          type="text"
          required="required"
          placeholder={retailerPhoneNo}
          name="retailerPhoneNo"
          value={editFormData.retailerPhoneNo}
          onChange={handleEditFormChange}
        ></input>
      </td>
      <td key={"retailerEmail_edit"}>
        <input
          type="text"
          required="required"
          placeholder={retailerEmail}
          name="retailerEmail"
          value={editFormData.retailerEmail}
          onChange={handleEditFormChange}
        ></input>
      </td>
      <td key={"retailerMemo_edit"}>
        <textarea
          type="text"
          required="required"
          placeholder={retailerDetail}
          name="retailerDetail"
          value={editFormData.retailerDetail}
          onChange={handleEditFormChange}
        ></textarea>
      </td>
      <td key={"button_edit"}>
        <button type="submit">
          {" "}
          <FontAwesomeIcon icon={faCheck} size="2x" />{" "}
        </button>
        <button type="button" onClick={handleCancelClick}>
          {" "}
          <FontAwesomeIcon icon={faTimes} size="2x" />{" "}
        </button>
      </td>
      <td key={"button_delete"}></td>
    </tr>
  );
}

export default EditRow;
