import styles from "../css/qnaReadEdit.module.css";

function QnaEdit({
  inquiryTitle,
  inquiryContent,
  inquiryAnswer,
  handleEditFormChange,
  onFormEditSubmit,
  onCancelClick,
}) {
  return (
    <form>
      <div className={styles.qnaBox}>
        <div className={styles.qnaBox__row}>{`${inquiryTitle}`}</div>
        <div className={styles.qnaBox__row}>
          <div className={styles.qnaBox__row_title}> </div>
          <div className={styles.qnaContent}>{`${inquiryContent}`}</div>
        </div>
        <div className={styles.qnaBox__row}>
          <div className={styles.qnaBox__row_title}> </div>
          <textarea
            type="text"
            onChange={handleEditFormChange}
            value={inquiryAnswer}
            name="inquiryAnswer"
            required
            className={styles.qnaContent}
          />
        </div>
        <input
          type="button"
          value=" "
          onClick={onFormEditSubmit}
          className={styles.qnaEditButton}
        />
        <input
          type="button"
          value=" "
          onClick={onCancelClick}
          className={styles.qnaEditButton}
        />
      </div>
    </form>
  );
}

export default QnaEdit;
